.loader {
  width: 32px;
  height: 90px;
  box-sizing: border-box;
  background-color: #fff;
  background-image: radial-gradient(#fff 34%, #ff3d00 35%, #ff3d00 54%, #fff 55%), linear-gradient(#ff3d00 10px, rgba(0, 0, 0, 0) 0);
  background-position: 50% 20px, 50% 2px;
  background-repeat: no-repeat;
  background-size: 28px 28px;
  border-bottom: 10px solid #ff3d00;
  border-radius: 50% 50% 0 0;
  margin: 20px auto;
  animation: animloaderBack 1s linear infinite alternate;
  display: block;
  position: relative;
}

.loader:before {
  content: "";
  box-sizing: border-box;
  width: 64px;
  height: 44px;
  border-radius: 50%;
  position: absolute;
  top: 67px;
  left: 50%;
  transform: translateX(-50%);
  box-shadow: inset 0 15px #ff3d00;
}

.loader:after {
  content: "";
  width: 34px;
  height: 34px;
  box-sizing: border-box;
  background: radial-gradient(#ffdf00 8%, rgba(249, 62, 0, .6) 24%, rgba(0, 0, 0, 0) 100%) -44px -44px / 100px 100px no-repeat;
  border-radius: 50% 50% 0;
  animation: animloader 1s linear infinite alternate;
  position: absolute;
  top: 112%;
  left: 50%;
  transform: translateX(-50%)rotate(45deg);
  box-shadow: 4px 4px 12px rgba(255, 61, 0, .5);
}

@keyframes animloaderBack {
  0%, 30%, 70% {
    transform: translateY(0);
  }

  20%, 40%, 100% {
    transform: translateY(-5px);
  }
}

@keyframes animloader {
  0% {
    width: 34px;
    height: 34px;
    background-position: -44px -44px;
    background-size: 100px 100px;
    box-shadow: 4px 4px 12px 2px rgba(255, 61, 0, .75);
  }

  100% {
    width: 30px;
    height: 28px;
    background-position: -36px -36px;
    background-size: 80px 80px;
    box-shadow: 2px 2px 8px rgba(255, 61, 0, .5);
  }
}

.hidden {
  display: none;
}

